<template>
  <v-app id="inspire">
    <v-main>
      <v-container class="fill-height" fluid>
        <v-row align="center" justify="center">
          <v-dialog v-model="isEnded" max-width="600">
            <v-card>
              <v-card-title class="headline py-4 d-flex align-center justify-center" rounded>NICE!</v-card-title>
              <v-card-text
                class="text-center"
              >Your timer has ended. I think you need to rest a bit :)</v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn class="success" @click="isEnded = false">EXIT</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-col cols="12" sm="8" md="4">
            <v-card class="elevation-12 py-4" rounded>
              <v-card-title
                class="headline py-4 d-flex align-center justify-center"
                rounded
              >SIMPLE POMODORO</v-card-title>
              <v-card-text class="display-4 text-center">{{ timestamp }}</v-card-text>
              <v-card-actions>
                <v-row align="center" justify="center">
                  <v-btn large class="success mr-2" @click="start()" :disabled="isStarted">
                    <v-icon dark left>mdi-play</v-icon>START
                  </v-btn>
                  <v-btn large class="error mr-2" @click="stop()" :disabled="!isStarted">
                    <v-icon dark left>mdi-stop</v-icon>STOP
                  </v-btn>
                  <v-btn large @click="restart()" :disabled="isStarted">
                    <v-icon dark left>mdi-refresh</v-icon>RESTART
                  </v-btn>
                </v-row>
              </v-card-actions>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>
<script>
export default {
  name: "Pomodoro",
  data: () => ({
    counter: 25 * 60 * 1000,
    timerInstance: null,
    isStarted: false,
    isEnded: false
    // miliseconds
  }),
  methods: {
    start() {
      this.isStarted = true;
      this.isEnded = false;
      this.timerInstance = setInterval(() => {
        this.counter -= 1000;
      }, 1000);
    },
    stop() {
      clearInterval(this.timerInstance);
      this.isStarted = false;
    },
    restart() {
      this.setCounter();
      this.isStarted = false;
    },
    setCounter() {
      this.counter = 25 * 60 * 1000;
    }
  },
  watch: {
    counter: function() {
      if (this.counter === 0) {
        clearInterval(this.timerInstance);
        this.setCounter();
        this.isStarted = false;
        this.isEnded = true;
      }
    }
  },
  computed: {
    timestamp: function() {
      return this.moment(this.counter).format("mm:ss");
    }
  }
};
</script>
